
.new{
    width: 100%;
    display: flex;

    .newContainer{
        flex: 6;

        .top, .bottom{
            box-shadow: -1px 5px 20px -1px rgba(0,0,0,0.53);
            -webkit-box-shadow: -1px 5px 20px -1px rgba(0,0,0,0.53);
            -moz-box-shadow: -1px 5px 20px -1px rgba(0,0,0,0.53);
            padding: 10px;
            margin: 20px;
            display: flex;

            h1{
                color: lightgray;
                font-size: 20px;
            }

            .left{
                flex: 1;
                text-align: center;


                img{
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    border: 1px solid gray;
                    object-fit: cover;
                }

            }
            .right{
                flex: 2;

                form{
                    display: flex;
                    flex-wrap: wrap;
                    gap: 20px;
                    justify-content: space-around;

                    .formInput{
                        width: 40%;


                        label{
                            display: flex;
                            align-items: center;
                            gap: 10px;


                            .icon{
                                cursor: pointer;
                            }

                        }

                        input{
                            width: 100%;
                            padding: 5px;
                            border: none;
                            border-bottom: 1px solid gray;
                        }
                    }

                    button{
                        width: 150px;
                        padding: 10px;
                        border: none;
                        background-color: teal;
                        color: white;
                        font-weight: bold;
                        cursor: pointer;
                        margin-top: 10px;
                        border-radius: 4px;



                    }
                }
            }
        }
    }
}